import React, {Component} from 'react';
import "./Home.css";
import Product from "./Product";
class Home extends Component {
    render() {
        return (
            <div className="home">
                <div className="home__container">
                    <img className="home__image" src="https://images-eu.ssl-images-amazon.com/images/G/02/digital/video/merch2016/Hero/Covid19/Generic/GWBleedingHero_ENG_COVIDUPDATE__XSite_1500x600_PV_en-GB._CB428684220_.jpg" alt="backgroundIMG"/>

                    <div className="home__row">
                        <Product title="The lean startup"
                                 price={29.99}
                                 image="https://www.amazon.fr/images/I/41+e3refnZL._SX334_BO1,204,203,200_.jpg"
                                 rating={4}
                                 id={Math.random()*5000}/>

                        <Product title="Kiano Elegance"
                                 price={169.99}
                                 image="https://www.amazon.fr/images/I/71BLs44GOJL._AC_UL480_FMwebp_QL65_.jpg"
                                 rating={3}
                                 id={Math.random()*5000}/>
                    </div>

                    <div className="home__row">
                        <Product title="La future table de Camille"
                                 price={89.99}
                                 image="https://www.amazon.fr/images/I/41GxsTmKvNL._AC_SY355_.jpg"
                                 rating={5}
                                 id={Math.random()*5000}/>
                        <Product title="Meilleur sweat"
                                 price={26.57}
                                 image="https://www.amazon.fr/images/I/71Bazv0RL9L._AC_UL480_FMwebp_QL65_.jpg"
                                 rating={5}
                                 id={Math.random()*5000}/>
                        <Product title="La Chaise Gaming lulw"
                                 price={159.99}
                                 image="https://www.amazon.fr/images/I/61meDRZr-JL._AC_UL480_FMwebp_QL65_.jpg"
                                 rating={4}
                                 id={Math.random()*5000}/>
                    </div>

                    <div className="home__row">
                        <Product title="Go regarder ou pas du coup ?"
                                 price={9.99}
                                 image="https://www.amazon.fr/images/I/71ATWcM4WDL._SS500_.jpg"
                                 rating={1}
                                 id={Math.random()*5000}/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;