import React, {useState} from 'react';
import "./Login.css";
import login_logo from "../assets/logo-amazon-sourire.png";
import {Link, useHistory} from "react-router-dom";
import {auth} from "../firebase";

const Login = () => {
    const history = useHistory();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const signIn = (e) => {
        e.preventDefault();
        //Firebase login
        auth.signInWithEmailAndPassword(email, password)
            .then(auth => {
                history.push('/');
            })
            .catch(error => alert(error.message))
    }

    const register = (e) => {
        e.preventDefault();
        //Firebase register
        auth.createUserWithEmailAndPassword(email, password)
            .then((auth) => {
                //if success
                if (auth) {
                    history.push('/');
                }
            })
            .catch(error => alert(error.message))

    }


    return (
        <div className="login">
            <Link to='/'>
                <img className="login__logo" src={login_logo} alt="amazonLogo"/>
            </Link>

            <div className="login__container">
                <h1>Sign in</h1>
                <form action="">
                    <h5>Email</h5>
                    <input type="text" value={email} onChange={e => setEmail(e.target.value)}/>

                    <h5>Password</h5>
                    <input type="password" value={password} onChange={e => setPassword(e.target.value)}/>

                    <button onClick={signIn} type="submit" className="login__signInBtn">Sign in</button>
                </form>
                <p>By signing in you agree to taking me on an interview</p>
                <button onClick={register} className="login__registerBtn">Create your amazon account</button>

            </div>
        </div>
    );
};

export default Login;
